@font-face {
  font-family: 'MyLocalFont'; /* Define a custom name for the font */
  src: url('../assets/fonts/Moneygraphy-Pixel.woff2') format('woff2'), url('../assets/fonts/Moneygraphy-Pixel.woff') format('woff');
}

body {
  margin: 0;
  padding: 0;
  font-family: 'MyLocalFont', Arial, sans-serif;
  font-size:16px;
  width: 100vw;
}

.vertical-scroll {
  display: flex;
  flex-direction: column;
  scroll-snap-type: y mandatory;
  width: 100vw;
  height: 100vh;
}

.vertical-scroll.no-scroll {
  overflow: hidden; /* Prevent scrolling when the class is active */
  height: 100vh; /* Lock the height to viewport */
}


.scroll-container {
  display: flex;
  flex-direction: column;
  width: 100%; /* Full width */
  height: auto;
  padding-bottom: 200px; /* Add padding to make space for the sticky container */
}

.step {
  flex: 0 0 100vh; /* Full viewport height */
  width: 100vw; /* Full width */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  scroll-snap-align: start;
  position: relative;
  transition: opacity 0.5s ease-in-out, transform 0.3s ease;/* Smooth opacity transition */
  opacity: 0.0; /* 기본 투명도 */
  z-index: 20;
  margin: 0 auto; /* Center the container horizontally */
  margin-top: -100px;
  margin-bottom: -100px;
}

.title-page {
  text-align: center;
  margin-bottom: 20px;
}

.title-page h1 {
  font-size: 2 rem;
  margin-bottom: 0.5rem;
}

.title-page h2 {
  font-size: 1.5rem;
  color: #555;
  margin-bottom: 1rem;
}

.title-page h3 {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 1rem;
  text-align: start;
  padding: 15px;
}

.title-page p {
  font-size: 1.2rem;
  margin: 0.5rem 0;
}

.description {
  max-width: 800px;
  margin: 2rem auto;
  color: #777;
  line-height: 1.5;
  text-align: justify;
  padding: 15px;
  font-size: 1rem;
  
}

.quote {
  max-width: 800px;
  margin: 2rem auto;
  color: #110000;
  line-height: 1.5;
  text-align: center;
  padding: 15px;

}

.text-container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

canvas {
  width: 95%; /* Full width of its container */
  height: auto; /* Maintain aspect ratio */
  display: block; /* Remove inline-block gap */
  margin-right: 20px;
}

.visualization-container.sticky {
  position: fixed;
  width: 100%;
  max-width: 800px;
  height: 50%;
  top: 50%; /* Center vertically */
  left: 50%; /* Center horizontally */
  transform: translate(-50%, -20%); /* Offset by 50% of its own dimensions */
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 8px;
  padding-right: 20px;
  flex-direction: column;
}

canvas {
  display: block;
}

.step.active {
  opacity: 1; /* Fully visible when active */
}

.step.exiting {
  opacity: 0; /* Fully transparent */
}

.image-container {
  display: flex;
  flex-wrap: wrap; /* Allows wrapping if the screen size is too small */
  gap: 20px; /* Space between items */
  justify-content: center; /* Center-align items */
}

.image-container > div {
  flex: 1 1 calc(33.333% - 20px); /* Each child takes 1/3 of the width minus the gap */
  max-width: 500px; /* Limit the maximum width */
  text-align: center;

}

.image-container img {
  width: 100%; /* Make images responsive */
  height: auto; /* Maintain aspect ratio */
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}
